@import (less) "common.less";

.menu{
  top: 0;
  background: #fff;
  height: 67px;
  width: 100%;
  margin: 0 auto;
  position: fixed;
  &-shortened{
    //width: 90%;
    border-bottom: 1px solid @light-grey;
  }
  img{
    float: left;
    padding: 10px 20px;
  }
  ul{
    float: right;
  }
  li {
    color: @midnightblue;
    list-style: none;
    display: inline;
    padding: 25px 20px;
    font-size: 17px;
    text-transform: uppercase;
    vertical-align: sub;
    &:hover{
      color: @white;
      background: @midnightblue;
      cursor: pointer;
    }
  }
  div{
    .clear();
  }
}

.content{
  width: 85%;
  margin: 100px auto;
  background: @clouds;
}
