.institutes{
  width: 100%;
  height: 100%;
  .institute{
    width: 100%;
    line-height: 45px;
    height: 50px;
    background: @greensea;
    color: @clouds;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    border-bottom: 1px solid @clouds;
    &:hover{
      background: @turquoise;
    }
    &:active{
      background: @wetasphalt;
    }
    &-current{
      background: @wetasphalt;
      &:hover{
        background: @wetasphalt;
      }
    }
  }
  .profiles{
    width: 100%;
    background: @light-grey;

    .profile{
      background: @greensea;
      color: @clouds;
      width: 200px;
      height: 100px;
      margin: 5px 10px 5px 0px;
      line-height: 90px;
      float: left;
      text-align: center;
      cursor: pointer;
      &:hover{
        background: @turquoise;
      }
      &:active{
        background: @wetasphalt;
      }
    }
  }
}